@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@200;300;400;600&display=swap);
/*Обнуление*/
*{padding:0;margin:0;border:0;}
*,*:before,*:after{box-sizing:border-box;}
:focus,:active{outline:none;}
a:focus,a:active{outline:none;}
nav,footer,header,aside{display:block;}
html,body{height:100%;width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%;}
input,button,textarea{font-family:inherit;}
input::-ms-clear{display:none;}
button{cursor:pointer;}
button::-moz-focus-inner{padding:0;border:0;}
a,a:visited{text-decoration:none;}
a:hover{text-decoration:none;}
ul li{list-style:none;}
img{vertical-align:top;}
h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:inherit;}
/*--------------------*/

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Source Code Pro', monospace;
}

.App {
  padding-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: linear-gradient(to right, #434343 0%, black 100%);
  width: 100%;
  height: 100vh;
}

h1 {
  color: white;
  font-size: 20px;
  text-transform: uppercase;
}



.GeneratorContainer_root__151oj {
    margin-top: 50px;
    width: 800px;
    height: 350px;
    padding-top: 80px;
    background-color: #ff4343;
    color: white;
}
.Button_button__3ATFO button {
    background-color: black;
    color: #ff4343;
    width: 220px;
    height: 35px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    text-transform: uppercase;
    transition: .3s;
}

.Button_button__3ATFO button:active {
    background-color: rgb(97, 97, 97);
    transform: scale(1.03);
}

.Button_button__3ATFO button:hover {
    animation: Button_button-move__2lX_D 0.2s ease-in-out;
}

@keyframes Button_button-move__2lX_D {
    0% {
        transform: translateX(0);
    }
    50% {
        transform: translateX(-2px);
    }
    100% {
        transform: translateX(3px);
    }
}
